
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgb(251, 210, 215, .8)), to(rgb(251, 210, 215, .2))), url('images/mountains.jpg');
  background-image: -webkit-linear-gradient(bottom, rgb(251, 210, 215, .8), rgb(251, 210, 215, .2)), url('images/mountains.jpg');
  background-image: -o-linear-gradient(bottom, rgb(251, 210, 215, .8), rgb(251, 210, 215, .2)), url('images/mountains.jpg');
  background-image: linear-gradient(0deg, rgb(251, 210, 215, .8), rgb(251, 210, 215, .2)), url('images/mountains.jpg');
  height: 35%;
  /* padding: 0px; */
  color: #222;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


nav {
  font-family: 'Tajawal', sans-serif;
  font-size: 18px;
  color: #222;
  margin-left: 3rem;
  padding: .5rem;
}

/**//**//**//**//**//**/
 /* ARROW ANIMATION */
.arrow {
  /* text-align: center; */
  margin-left: 45%;
  margin-top: 2rem;
  width: 10%;
  position: relative;
  -webkit-animation-name: down;  /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 3s;  /* Safari 4.0 - 8.0 */    
  -webkit-animation-delay: 2s; /* Safari 4.0 - 8.0 */
  -webkit-animation-fill-mode: backwards; /* Safari 4.0 - 8.0 */
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  animation-name: down;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;    
  -webkit-animation-delay: 3s;    
          animation-delay: 3s;
  animation-fill-mode: backwards;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes down {
  to {top: 0px;}
  from {top: 50px;}
}

@keyframes down {
  from {top: 0px;}
  to {top: 50px;}
}


