@import url('https://fonts.googleapis.com/css?family=Tajawal:200');


.footer {
    background-image: -webkit-gradient(linear, left bottom, left top, from(rgb(251, 210, 215, 1)), to(rgb(251, 210, 215, 0)));
    background-image: -webkit-linear-gradient(bottom, rgb(251, 210, 215, 1), rgb(251, 210, 215, 0));
    background-image: -o-linear-gradient(bottom, rgb(251, 210, 215, 1), rgb(251, 210, 215, 0));
    background-image: linear-gradient(0deg, rgb(251, 210, 215, 1), rgb(251, 210, 215, 0));
    padding: 40px;
    color: #222;
    font-family: 'Tajawal', sans-serif;
    font-size: 18px;
  }

  .footer-text {
    margin: auto;
    text-align: center;
    line-height: 120px;
  }

  .footer-icons {
    width: 35px;
  }

  .footer-flex-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    width: 300px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }


